document.addEventListener('DOMContentLoaded', ()=>{
    galeriaImagenes();
    //agrandarImagen();
})

function galeriaImagenes(){
    const listaImagenes = document.querySelector('.galeria-imagenes');
    
    for (let i = 0; i < 12; i++) {
        const picture = document.createElement('PICTURE');
        picture.classList.add('image');
        picture.setAttribute('id', `image-${i + 1}`);

        
        imagenes(i + 1, 'thumb', picture);
        listaImagenes.appendChild(picture);

        picture.addEventListener('click', ()=>{
            mostrarImagen(i + 1);
        })
        }

}

function mostrarImagen(id){
    //Crear div para la imagen y el fondo
    const body = document.body;
    body.classList.add('fijar-body');
    const overlay = document.createElement('DIV');
    overlay.classList.add('overlay');
    body.appendChild(overlay);

    //Crear la imagen para mostrar
    picture = document.createElement('PICTURE');
    imagenes(id);
    overlay.appendChild(picture);

    //Crear div de botones
    const divBotones = document.createElement('DIV');
    divBotones.classList.add('div-botones');

    //Boton retroceder
    const botonIzquierda = document.createElement('P');
    botonIzquierda.textContent = '<';
    botonIzquierda.addEventListener('click', ()=>{
        id--

        if (id < 1) {
            id = 12
        }
        imagenes(id)
    })

    //Boton cerrar
    const botonCerrar = document.createElement('P');
    botonCerrar.textContent = 'X';
    botonCerrar.addEventListener('click', ()=>{
        overlay.remove();
        body.classList.remove('fijar-body');
    })

    //Boton Avanzar
    const botonDerecha = document.createElement('P');
    botonDerecha.textContent = '>';
    botonDerecha.addEventListener('click', ()=>{
        id++

        if (id > 12) {
            id = 1
        } 
        imagenes(id)
    })

    overlay.appendChild(divBotones);

    divBotones.appendChild(botonIzquierda);
    divBotones.appendChild(botonCerrar);
    divBotones.appendChild(botonDerecha);

    const botones = document.querySelectorAll('.div-botones p');
    botones.forEach(boton => {
        boton.classList.add('boton-imagenes');
    });
}


function imagenes(id, type = 'grande', pictureElement = picture) {
    pictureElement.innerHTML =
    `<source srcset="build/img/${type}/${id}.avif" type="image/avif">
    <source srcset="build/img/${type}/${id}.webp" type="image/webp">
    <img loading="lazy" width="200" height="300" src="build/img/${type}/${id}.jpg" alt="Concert Image">`;
}